  padding: 0;
  font-family: 'Roboto', sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #000000), color-stop(30%, #010A10), color-stop(75%, #59230B), to(#2F1107));
  background: -webkit-linear-gradient(top, #000000 5%, #010A10 30%, #59230B 75%, #2F1107 100%);
  background: -o-linear-gradient(top, #000000 5%, #010A10 30%, #59230B 75%, #2F1107 100%);
  background: linear-gradient(to bottom, #000000 5%, #010A10 30%, #59230B 75%, #2F1107 100%);
}

h1 {
