.employment-field {
  pointer-events: none;
  margin: 10px 90px 0 50px;
  white-space: nowrap;
  overflow: hidden;
  font-size: 1.64em;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-name: fadeAndScale;
          animation-name: fadeAndScale;
  -webkit-animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
          animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
}

.employment-field span {
  display: inline-block;
  opacity: 0;
  -webkit-transform: translate(-150px, 0) scale(.3);
      -ms-transform: translate(-150px, 0) scale(.3);
          transform: translate(-150px, 0) scale(.3);
  -webkit-animation: leftRight 4s forwards;
          animation: leftRight 4s forwards;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

@keyframes leftRight {
	40% {
		-webkit-transform: translate(50px, 0) scale(.7);
		        transform: translate(50px, 0) scale(.7);
		opacity: 1;
		color: #2c6934;
	}
	60% {
		color: #161975;
	}
	80% {
		-webkit-transform: translate(0) scale(2);
		        transform: translate(0) scale(2);
		opacity: 0;
	}
	100% {
		-webkit-transform: translate(0) scale(1);
		        transform: translate(0) scale(1);
		opacity: .3;
	}
}

@-webkit-keyframes leftRight {
	40% {
		-webkit-transform: translate(50px, 0) scale(.7);
		        transform: translate(50px, 0) scale(.7);
		opacity: 1;
		color: #2c6934;
	}
	60% {
		color: #161975;
	}
	80% {
		-webkit-transform: translate(0) scale(2);
		        transform: translate(0) scale(2);
		opacity: 0;
	}
	100% {
		-webkit-transform: translate(0) scale(1);
		        transform: translate(0) scale(1);
		opacity: .3;
	}
}

.employment-field span:nth-of-type(2) {
	-webkit-animation-delay: .05s;
