@keyframes fadeAndScale {
    from {
        opacity: 0;
        -webkit-transform: scale(.1, .1);
                transform: scale(.1, .1);
    }
    to {
        opacity: 1;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }
}

@-webkit-keyframes fadeAndScale {
    from {
        opacity: 0;
        -webkit-transform: scale(.1, .1);
                transform: scale(.1, .1);
    }
    to {
        opacity: 1;
        -webkit-transform: scale(1, 1);
                transform: scale(1, 1);
    }
}

