#projects {
  text-align: center;
  background: #14212b;
  padding-bottom: 40px;
  width: 100%;
}

#projects h2 {
  color: #989191;
  text-align: center;
  font-weight: 700;
  font-size: 1.8em;
  margin: 25px 0 25px;
  width: 100%;
}

.projects-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;;
  margin: auto;
  max-width: 1200px;
}

.each-project {
  position: relative;
  border: 2px solid #18261d;
  overflow: hidden;
  margin: 10px;
  max-width: 47%;
  min-width: 400px;
  max-height: 350px;
}

.each-project .project-image {
  display: block;
  width: 100%;
  height: 100%;
}

.each-project:hover .project-image {
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3)
}

.article-layer {
  background: rgb(206, 206, 206);;
  -webkit-transition: opacity 1000ms;
  -o-transition: opacity 1000ms;
  transition: opacity 1000ms;
  opacity: 0;
  position: absolute;
  top: 0;
  text-align: center;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.article-layer:hover {
  opacity: .9;
}

.article-layer h3 {
  padding: 25px 0 15px 0;
  margin-bottom: 20px;
  text-align: center;
  font-size: 2em;
  font-weight: 700;
}

.article-layer p {
  margin: 0 auto;
  text-align: left;
  width: 90%;
}

.article-layer .project-icons {
  padding: 20px 50px;
  width: 60px;
}

.article-layer .project-button {
  position: relative;
  top: 30px;
  height: 35px;
  width: 100px;
  border: none;
  border-radius: 5px;
  background: #000;
  color: #FFF;
  cursor: pointer;
  outline: none;
  margin: 0 auto;
}

@media (max-width:847px) {
  .each-project {
    min-width: 90%;
    max-height: 430px;
  }
}

@media (max-width:460px) {
  .article-layer h3 {
    margin-bottom: 0;
    font-size: 1.8em;
    padding-bottom: 5px;
  }
}

@media (max-width:375px) {
  .each-project {
    max-height: 190px;
  }
  
  .article-layer h3 {
    padding-top: 10px;
    font-size: 1.65em;
  }
  .article-layer .project-icons {
    padding: 10px 30px;
    width: 50px;
  }
  scroll-btn {
    bottom: 0;
    position: absolute;
    right: 43%;
  }
}



